import { useEffect, useState } from 'react';
import './About.css';

const About = () => {

    const [search, setSearch] = useState('fire');
    const [image, setImage] = useState([]);

    useEffect(() => {
        fetchImage();
    }, {});

    const fetchImage = async () => {
        const url = "https://api.unsplash.com/search/photos?query=" + search + "&client_id=6fa91622109e859b1c40218a5dead99f7262cf4f698b1e2cb89dd18fc5824d15&per_page=30&orientation=portrait&page=1";
        console.log('iiaisajfjabaaaaaaaa' + url);
        const data = await fetch(url);
        const images = await data.json();
        setImage(images.results);
        console.log(data);
        console.log(images.results);
    }
    const onSumitPressed = ({ e }) => {
        if (search.length < 1) {
            alert('please enter something');
            return;
        }
        fetchImage();

    }

    return (
        <div className='container'>
            <h1>UnSplash Images</h1>
            <form className='search-container'>
                <input className='search-container-input' type="text" onChange={(e) => { setSearch(e.target.value) }} />
                <input className='search-container-button' type="button" onClick={(e) => onSumitPressed(e)} value="Submit" />
            </form>

            {image.map((img) => {
                return (
                    <>
                        <img src={img.urls.small} alt="" />
                    </>
                );
            })}
        </div>
    )
}

export default About
