import { useEffect, useState } from 'react';
import './Home.css';

const Home = () => {

    const [items, setItems] = useState([]);
    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        const data = await fetch('http://newsapi.org/v2/everything?q=tesla&from=2021-01-16&sortBy=publishedAt&apiKey=efb6dd69dbfe46d28e88b4104bc01ab2');
        const news = await data.json();
        console.log(news);
        setItems(news.articles);
    }



    return (
        <div className='news-container'>
            <h1>Latest Updates</h1>
            {
                items.length < 1 ?
                    <dir>Loading</dir> :
                    <div>
                        {
                            items.map((item) => {
                                return (
                                    <div className='news-card'>
                                        <img style={{ width: '40%' }} src={item.urlToImage} alt="" />
                                        <div className='news-card-content'>
                                            <h3>{item.title}</h3>
                                            <p>{item.description}</p>
                                            <h5>Article by: {item.author}</h5>
                                        </div>

                                        {/* <h1>{item.author}</h1> */}

                                    </div>
                                );
                            })
                        }
                    </div>

            }
        </div>
    )
}

export default Home
